import axios from "axios";

export const ARTICLES = "ARTICLES";

export const articles = (articles) => {
    return {
        type: "ARTICLES",
        articles: articles
    }
}

export const fetchArticles = () => {
    return dispatch => {
      axios.get("http://localhost:9000/articles")
      .then(data => dispatch(articles(data.data.articles))).catch(err => console.log(err));
    };
  }