import React, { Component } from 'react';

class NewArticle extends Component {
    state = {
        data:{
            title:"",
            body:"",
            category:""
        }
    };
    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);
        } 
    

    handleChange = e => {
    e.preventDefault();
    this.setState({
      ...this.state,
      data: { ...this.state.data, [e.target.name]: e.target.value }
    });
    };
    
    handleSubmit(event) {
        event.preventDefault();
        console.log(this.state.data);
        
      }
    
      render() {
        return (
            <>
          <form onSubmit={this.handleSubmit}>
            <label>
              Title:
              <input type="text" name="title" value={this.state.data.title} onChange={this.handleChange} />
            </label><br/>
            <label>
              Body:
              <textarea 
              type='text' 
              name="body" 
              placeholder="Remember, make it interesting" 
              cols="100" rows="5" 
              value={this.state.data.body} onChange={this.handleChange} 
              class="form-control" id="exampleFormControlTextarea5"
              />
            </label><br/>
            <label>
            Category:
            <input name="category" value={this.state.data.category} onChange={this.handleChange}>
                
            </input>
            </label><br/>
            <input type="submit" value="Submit" />
          </form>
          </>
        );
    }
}

export default NewArticle;